.game-board {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-wrap: wrap;
	    flex-wrap: wrap;
	-ms-flex-pack: center;
	    justify-content: center;
	width: 50%;
}

.game-container {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: center;
	    justify-content: center;
	padding: 50px 0px 50px;

}


@media only screen and (max-width: 1000px) {
	.game-board {
		width: 75%;
	}
}

@media only screen and (max-width: 700px) {
	.game-board {
		width: 90%;
	}
}


