.square {
	width: 25%;
	padding: 5px;
}

.square img {
	width: 100%;
	height: 100%;
	border-radius: 5px;
	-webkit-box-shadow: #635f62 17px 10px 68px -28px;
	        box-shadow: #635f62 17px 10px 68px -28px;
}

.square:hover {
	-webkit-animation: squares 0.5s forwards;
	        animation: squares 0.5s forwards;
	cursor: pointer;
}

@-webkit-keyframes squares {
	from {-webkit-transform: scale(1);transform: scale(1);}
	to{-webkit-transform: scale(1.1);transform: scale(1.1);}
}

@keyframes squares {
	from {-webkit-transform: scale(1);transform: scale(1);}
	to{-webkit-transform: scale(1.1);transform: scale(1.1);}
}

@media only screen and (max-width: 1000px) {
	.square {
		width: 33.333%;
	}
}

@media only screen and (max-width: 700px) {
	.square {
		width: 50%;
	}
}


